import React from 'react';
import {Link} from "react-router-dom";

// Style sheets
import Style from './Input.module.scss';

export function Input(props){
    const location = props.location;

    return(
        <React.Fragment>
            <Link aria-label="Close" to="/"><button className={Style.Button} onClick={props.onExit}><i className="fas fa-times"></i></button></Link>
            <section className={Style.Search}>
                <h2 className={Style.Header}>Enter city</h2>
                <input 
                    className={location === "Incorrect Input" || location === "City already inserted" 
                        ? `${Style.TextField} ${Style.Error} animated shake` : location === "Correct Input" 
                            ? `${Style.TextField} ${Style.Correct}` : Style.TextField}
                    type="text" 
                    value={location} 
                    onFocus={props.onFocus}
                    placeholder='Try "London"' 
                    onChange={props.onChange} 
                    onKeyPress={props.onKeyPress}
                />
                <button 
                    aria-label="Submit location"
                    className={location === "Incorrect Input" || location === "City already inserted" 
                        ? `${Style.Submit} ${Style.Error}` : location === "Correct Input" 
                            ? `${Style.Submit} ${Style.Correct}` : Style.Submit}
                    onClick={props.onClick}>Submit</button>
            </section>
        </React.Fragment>
    );
}