import React from 'react';

// Style sheets
import Style from './TempGroup.module.scss';

export function TempGroup(props){
    const location = props.location;

    return(
        <div className={Style.TempGroup}>
            <h6 className={Style.TempOther}><i className="fas fa-caret-down"></i>{Math.round(location.main.temp_min)}</h6>
            <h1 className={Style.Temp}>{Math.round(location.main.temp)}</h1>
            <h6 className={Style.TempOther}><i className="fas fa-caret-up"></i>{Math.round(location.main.temp_max)}</h6>
        </div>
    );
}