import React from 'react';

// Style sheets
import Style from './TempInfo.module.scss';

export function TempInfo(props){
    const location = props.location;
    const tempType = props.tempType;

    return(
        <div className={Style.TempInfo}>
            <div className={Style.TempInfoItem}>
                <h6 className={Style.TempInfoHeader}>Feels Like:</h6>
                <h4 className={Style.TempInfoData}>{Math.round(location.main.feels_like)}</h4>
            </div>
            <div className={Style.TempInfoItem}>
                <h6 className={Style.TempInfoHeader}>Humidity:</h6>
                <h4 className={Style.TempInfoData}>{location.main.humidity}%</h4>
            </div> 
            <div className={Style.TempInfoItem}>
                <h6 className={Style.TempInfoHeader}>Wind:</h6>
                <h4 className={Style.TempInfoData}>
                    {location.wind.deg >= 0 && location.wind.deg < 22.5 
                        ? "N" : location.wind.deg >= 22.5 && location.wind.deg < 67.5 
                            ? "NE" : location.wind.deg >= 67.5 && location.wind.deg < 112.5 
                                ? "E" : location.wind.deg >= 112.5 && location.wind.deg < 157.5 
                                    ? "SE" : location.wind.deg >= 157.5 && location.wind.deg < 202.5 
                                        ? "S" : location.wind.deg >= 202.5 && location.wind.deg < 247.5 
                                            ? "SW" : location.wind.deg >= 247.5 && location.wind.deg < 292.5 
                                                ? "W" : location.wind.deg >= 292.5 && location.wind.deg < 337.5 
                                                    ? "NW" : "N" } {location.wind.speed}<span>{tempType === "metric" ? " km/h" : " mph"}</span></h4>
            </div>
        </div>
    );
}