import React from 'react';
import {HashRouter as Router, Route} from "react-router-dom";
import PropTypes from 'prop-types';

// Components
import {Input} from '../components/Input/Input';
import {Locations} from '../components/Locations/Locations';
import {Location} from '../components/Location/Location';

// Style sheets
import Style from './App.module.scss';

export default class App extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      selectedLocationText: "",
      newLocationSelected: null,
      locations: [],
      cities: [],
      locationData: null,
      tempType: "metric",
    }
  }

  static propTypes = {
    selectedLocationText: PropTypes.string,
    newLocationSelected: PropTypes.object,
    locations: PropTypes.array,
    cities: PropTypes.array,
    locationData: PropTypes.object,
    tempType: PropTypes.string,
  }

  componentDidMount(){ 
    // Will set a variables with the value of the local storage 
    const setLocations = JSON.parse(localStorage.getItem("Locations") || "[]");
    const setCities = JSON.parse(localStorage.getItem("Cities") || "[]");

    this.setState(state => ({
      locations: setLocations,
      cities: setCities,
    }))
  }

  // Method to handle input change from the user
  handleChange = (event) => {
    this.setState({
      selectedLocationText: event.target.value,
    })
  }
  
  // Method to handle the submit button once it is clicked
  handleClick = () => {
    const location = this.state.selectedLocationText; 
    const key = "f3ef21c55d165f4433893081d602af6e";
    const units = this.state.tempType;


    fetch(`https://api.openweathermap.org/data/2.5/weather?q=${location}&APPID=${key}&units=${units}`)
    .then(response => response.json())
    .then(data => {
      this.setState({
        newLocationSelected: data,
      })
      this.handleUpdate();
    })
    .catch(Error);
  }
  
  // Method to update the state once the submit button is clicked and the api is successfully fetched
  handleUpdate = () => {
    const errors = ["400", "401", "403", "404", "405", "409", "500", "503"];

    // Getting current state of locations list
    const newLocations = [...this.state.locations];

    // Getting current state of cities list & current input by user
    const currentCities = [...this.state.cities];
    
    // Checking if an error has occurred to display the incorrect message
    if(this.state.newLocationSelected.cod === errors.find(number => number === this.state.newLocationSelected.cod)){
      this.setState({
        selectedLocationText: "Incorrect Input",
        newLocationSelected: null,
      })
      return
    }else if(this.state.newLocationSelected.name.toLowerCase() === this.state.cities.find(name => name === this.state.newLocationSelected.name.toLowerCase())){
      this.setState({
        selectedLocationText: "City already inserted",
        newLocationSelected: null,
      })
    }else{
      // Adding the new location selected in to the locations Array
      newLocations.push(this.state.newLocationSelected);
      
      // Adding the input from the user to the new cities list
      currentCities.push(this.state.selectedLocationText.toLowerCase());

      // Setting the Locations array into our local storage
      localStorage.setItem("Locations", JSON.stringify(newLocations));

      // Setting the Cities array into our local storage
      localStorage.setItem("Cities", JSON.stringify(currentCities));
      
      // Setting the locations array with the new locations array & the cities array with the new cities array
      this.setState({
        selectedLocationText: "Correct Input",
        newLocationSelected: null,
        locations: newLocations,
        cities: currentCities,
      })
    }
  }

  // Method to handle when the user clicks on the input 
  handleFocus = () => {
    if(this.state.selectedLocationText === "Incorrect Input" || this.state.selectedLocationText === "Correct Input" || this.state.selectedLocationText === "City already inserted"){
      this.setState({
        selectedLocationText: "",
      })
    }
  }

  // Method to reset the input
  handleExit = () => {
    this.setState({
      selectedLocationText: "",
      newLocationSelected: null,
    })
  }

  // Method to handle the keypress by the user
  handleKeyPress = (event) => {
    this.handleFocus(); 
    if(event.key === 'Enter'){
      this.handleClick(); 
    }
  }

  // Method to delete the location from the state
  // handleDelete = (id, name) => {
  //   const itemId = id;
  //   const itemName = name.toLowerCase();

  //   // Getting the current state of locations array
  //   const currentlocations = [...this.state.locations];

  //   // Remove the item from the new locations array
  //   const newlocations = currentlocations.filter(item => {
  //     return item.id !== itemId
  //   });

  //   // Getting the current state of cities array
  //   const currentCities = [...this.state.cities];
    
  //   // Remove the item from the new cities array
  //   const newCities = currentCities.filter(item => {
  //     return item !== itemName
  //   });

  //   this.setState({
  //     locations: newlocations,
  //     cities: newCities,
  //   })
  // }

  handleClearLocations = () => {
    // Removing the Locations & Cities arrays from the local storage
    localStorage.removeItem("Locations");
    localStorage.removeItem("Cities");
    this.setState({
      locations: [],
      cities: [],
    })
  }

  // Method to change the wind and the tempreture to either the Metric system or the Imperial system 
  handleTempType = () => {
    // Getting the current state of locations array
    const currentlocations = [...this.state.locations];

    // Getting the current state of locationData array
    const currentlocationData = (this.state.locationData === null) ? null : JSON.parse(JSON.stringify(this.state.locationData));

    // Checks whether the current temp type is metric or not
    if(this.state.tempType === "metric"){
      currentlocations.map(item => {
        return (
          item.main.temp = item.main.temp * 1.8 + 32, 
          item.main.feels_like = item.main.feels_like * 1.8 + 32, 
          item.main.temp_min = item.main.temp_min * 1.8 + 32, 
          item.main.temp_max = item.main.temp_max * 1.8 + 32,
          item.wind.speed = item.wind.speed / 1.6,
          item.wind.speed = Math.round(item.wind.speed)
          );    
        })
        
        this.setState({
          locations: currentlocations,
          tempType: "imperial"
        })

        if(currentlocationData === null){
          return
        } else{
          currentlocationData.list.map(item => {
            return (
              item.main.temp = item.main.temp * 1.8 + 32
            );    
          })
          
          this.setState({
            locationData: currentlocationData,
          })
        }
        
    }else {
      currentlocations.map(item => {
        return (
          item.main.temp = (item.main.temp - 32) / 1.8,
          item.main.feels_like = (item.main.feels_like - 32) / 1.8,
          item.main.temp_min = (item.main.temp_min - 32) / 1.8,
          item.main.temp_max = (item.main.temp_max - 32) / 1.8,
          item.wind.speed = item.wind.speed * 1.6,
          item.wind.speed = Math.round(item.wind.speed)
        );
      })

      this.setState({
        locations: currentlocations,
        tempType: "metric"
      })

      if(currentlocationData === null){
        return
      } else{
        currentlocationData.list.map(item => {
          return (
            item.main.temp = (item.main.temp - 32) / 1.8
            );    
          })
        
        this.setState({
          locationData: currentlocationData,
        })
      }

    }
  }

  // Method to render the location chosen by the user in /location
  handleClickLocation = (location) => {
    const locationName = location.name
    const country = location.sys.country; 
    const key = "f3ef21c55d165f4433893081d602af6e";
    const units = this.state.tempType;

    fetch(`https://api.openweathermap.org/data/2.5/forecast?q=${locationName},${country}&APPID=${key}&units=${units}`)
        .then(response => response.json())
        .then(data => {
            this.setState({
              newLocationSelected: location,
              locationData: data
            })
        })
        .catch(Error);
  }

  render(){
    return (
      <Router>
        <div className={`${Style.Horizontal} animated fade`}>
          <div className={`${Style.Message} animated flash`}>
            <h1 className={Style.Header}>Please rotate your device</h1>
            <h4 className={Style.Subheader}>We don't support landscape mode yet on mobile. Please go back to portrait mode for the best experience. Thank you.</h4>
          </div>
        </div>
        <Route exact path="/">
          <div className={`${Style.Container} animated fade`}>
            <Locations 
              location={this.state.locations} 
              tempType={this.state.tempType} 
              onClick={this.handleClickLocation} 
              onClickDelete={this.handleDelete} 
              onClickClearLocations={this.handleClearLocations} 
              handleClickTempType={this.handleTempType} />
          </div>
        </Route>
        <Route path="/addlocation">
          <div className={`${Style.Container} animated fade`}>
            <Input 
              location={this.state.selectedLocationText} 
              onChange={this.handleChange} 
              onClick={this.handleClick} 
              onFocus={this.handleFocus} 
              onExit={this.handleExit} 
              onKeyPress={this.handleKeyPress} />
          </div>
        </Route>
        <Route path={this.state.newLocationSelected === null ? "/location" : `/${this.state.newLocationSelected.name}`}>
          <div className={`${Style.Container} animated fade`}>
            <Location location={this.state.newLocationSelected} data={this.state.locationData} tempType={this.state.tempType} handleClickTempType={this.handleTempType} />
          </div>
        </Route>
      </Router>
    );
  }
}