import React from 'react';
import {Link} from "react-router-dom";

// Components
import {LocationButton} from '../LocationButton/LocationButton';

// Style sheets
import Style from './Locations.module.scss';

export function Locations(props){
    const locations = props.location;
    const tempType = props.tempType;

    return(
        <React.Fragment>
            <header className={Style.Header}>
                <button aria-label={"Clear Locations List"} onClick={props.onClickClearLocations} className={`${Style.Button} ${Style.ClearBtn}`}>Clear</button>
                <button aria-label={tempType === "metric" ? "Change units to fahrenheit" : "Change units to celcius"} className={Style.Button} onClick={props.handleClickTempType}>
                    <span className={tempType === "metric" ? Style.Active : Style.Inactive}><span>&#176;</span>C</span> / <span className={tempType === "metric" ? Style.Inactive : Style.Active}><span>&#176;</span>F</span></button>
            </header> 
            <section className={Style.LocationsSection}>
                <div className={Style.LocationsHeader}>
                    <h1 className={Style.PageHeader}>Locations</h1>
                    <Link aria-label="Add new location" to="/addlocation" className={Style.Button}><i className="fas fa-plus"></i></Link>
                </div>
                <section className={Style.Locations}>
                    {
                        locations.map((location) => {
                            return (
                                <LocationButton key={location.id} tempType={tempType} data={location} onClick={props.onClick} onClickDelete={props.onClickDelete} />
                            );
                        })
                    }
                </section>
            </section>
        </React.Fragment>
    );
}